<template>
    <div class="app-register">
      <div class="header">
        <router-link to="/home"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
        <span></span>
        <span><i ></i></span>
      </div>
      <div class="regDiv">
        <div><span>:</span><input type="text" placeholder="()"></div>
        <div><span>:</span><input type="password" placeholder="()"></div>
        <div><span>:</span><input type="password" placeholder="()"></div>
        <div><span>:</span><input type="text" placeholder="()"></div>
        <div><span>:</span><input type="text" placeholder="()"></div>
        <div><span>:</span><input type="text" placeholder="()"></div>
        <div class="gender">
          <span>:</span>
          <div>
            <label for="m" ><input type="radio" name="gender" id="m"></label>&nbsp;
            <label for="fm" ><input type="radio" name="gender" id="fm"></label>
          </div>
        </div>
        <div class="question">
          <span>:</span>
          <input type="text" placeholder="">
        </div>
        <div><span>:</span><input type="text" placeholder=""></div>
      </div>
      <div class="agree">
        <input type="checkbox">
        <span> <router-link to="">&lt;&lt;&gt;&gt;</router-link></span>
      </div>
      <mt-button size="large" class="regBtn"></mt-button>
    </div>
</template>

<script>
    export default {
        name: "register"
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin:0px;
  }
  i{
    font-size:1.5rem;
    color:white;
  }
  .app-register .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .regDiv{
    margin: 0.5rem;
    padding:0 0.5rem;
    border:1px solid #aaaaaa;
    background: white;
  }
  .regDiv div {
    height:3rem;
    border-bottom:1px solid #aaaaaa;
    display:flex;
    align-items:center;
    justify-content: flex-start;
  }
  .regDiv div span{
    width:30%;
    text-align:left;
    height:3rem;
    line-height: 3rem;
  }
  .regDiv div input:not([type=radio]){
    width:70%;
    height: 2.9rem;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }
  .regDiv .gender div{
    width: 70%;
  }
  .regBtn {
    background: #019f1d;
    color:white;
    font-size:1.4rem;
    margin: 0.5rem 0;
  }
</style>



